* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: "PT Sans", sans-serif;
}

@media screen and (max-width: 768px) {
	.martinique,
	.villa,
	.home {
		font-size: 67px !important;
	}

	.hero-contianer p {
		text-align: center !important;
	}
}

.home,
.villa,
.products,
.sign-up,
.martinique {
	display: flex;
	height: 90vh;
	align-items: center;
	justify-content: center;
	font-size: 3rem;
}

.villa {
	background-image: url("https://www.locations-vue-turquoise.com/images/locations/diaporama/location-martinique-villa-piscine.jpg");
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	color: #fff;
	font-size: 100px;
	margin-top: -80px;
}

.martinique {
	background-image: url("https://images.unsplash.com/photo-1570871821811-4f5504c08c4c?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1548&q=80");
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	color: #fff;
	font-size: 100px;
	margin-top: -80px;
}

.products {
	background-image: url("/images/img-1.jpg");
	background-position: center;
	background-size: fill;
	background-repeat: no-repeat;
	color: #fff;
	font-size: 100px;
	margin-top: -80px;
}

.sign-up {
	background-image: url("/images/img-8.jpg");
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	color: #fff;
	font-size: 100px;
}
