.content {
	padding: 4rem;
	background: #fff;
	font-family: "Roboto";
}

h1 {
	text-align: center;
}

.content__container {
	display: flex;
	flex-flow: column;
	align-items: center;
	max-width: 1120px;
	width: 90%;
	margin: 0 auto;
	font-family: "Roboto";
}

.specs {
	margin-top: 30px;
}
.specs span {
	font-family: "Roboto";
}

.cards__container {
	display: flex;
	flex-flow: column;
	align-items: center;
	max-width: 1120px;
	width: 90%;
	margin: 0 auto;
}

.cards__wrapper {
	position: relative;
	margin: 50px 0 45px;
}

.cards__items {
	margin-bottom: 24px;
}

.container-pictures {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	margin-bottom: 40px;
	margin-top: 40px;
}

.container-pictures .picture {
	width: 445px;
	height: 445px;
	margin: 15px 15px;
	z-index: 1000;
}

.picture img {
	position: relative;
	z-index: 1;
	transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
	border-radius: 7px;
}

.picture img:hover {
	transform: scale(1.05);
	box-shadow: rgb(0 0 0 / 80%) 0px 5px 4px -3px, rgb(0 0 0 / 30%) 0px 5px 7px -3px;
	cursor: pointer;
}
